<template>
  <div class="persondata padd">
      <img src="../../assets/avatar.jpg" alt="" class="avatar">
      <p class="two_t_l"><span>点击修改头像</span></p>
      <ul>
          <li>
              <span class="two_t_m">昵称</span>
              <input type="text" placeholder="请输入" class="two_t_h">
          </li>
          <li>
              <span class="two_t_m">性别</span>
              <input type="radio" style="display:none;" id="one" value="1" v-model="sex">
              <label for="one" class="iconfont" :class="[sex==1?'icon-gouxuan':'icon-weixuanzhong']"><span>男</span></label>
              <input type="radio" style="display:none;" id="two" value="0" v-model="sex">
              <label for="two" class="iconfont" :class="[sex!=1?'icon-gouxuan':'icon-weixuanzhong']"><span>女</span></label>
          </li>
          <li>
              <span class="two_t_m">手机</span>
              <input type="text" class="two_t_h" readonly placeholder="请输入" value="186****6666">  
              <span class="changeNumber two_t_m">| 更改号码</span>
          </li>
          <li>
              <span class="two_t_m">生日</span>
              <input type="text" @click="chooseTime()" class="two_t_h" readonly placeholder="请选择" :value="dateTime">  
          </li>
      </ul>
      <div class="bottom">
          <p class="three_t_l">* 在生日当天下单，得乐将为您送出惊喜礼品一份。</p>
          <button @click="$router.push('/personalCenter/changenumber')" class="bluebtn">提交</button>
      </div>
        <group style="display:none;">
            <datetime
                v-model="dateTime"
                title="生日"
                :min-year="1917"
                :max-year="2017"
                :show="showDate"
                @on-hide="showDate=false">
            </datetime>
        </group>
  </div>
</template>

<script>
import { Datetime, Group } from "vux";

export default {
  name: "Persondata",
  data() {
    return {
      sex: 1,
      showDate: false,
      dateTime: ""
    };
  },
  methods: {
    chooseTime() {
      this.showDate = true;
    }
  },
  components: {
    Datetime,
    Group
  }
};
</script>

<style scoped lang="less">
.persondata {
  font-size: 0.16rem;
  .avatar {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
  }
  p {
    text-align: center;
    margin: 0.1rem 0 0.15rem 0rem;
  }
  ul {
    li {
      border-bottom: 0.01rem solid #f2f2f2;
      height: 0.52rem;
      line-height: 0.51rem;
      position: relative;
      input {
        border: none;
        margin-left: 0.46rem;
      }
      label {
        width: 0.22rem;
        height: 0.22rem;
        font-size: 0.22rem;
        line-height: 0.22rem;
        margin-right: 0.2rem;
        margin-left: 0.46rem;
        margin-top: 0.1rem;
        position: relative;
        span {
          font-size: 0.16rem;
          line-height: 0.16rem;
          position: absolute;
          top: 0.02rem;
          left: 0.3rem;
        }
      }
      .changeNumber {
        position: absolute;
        right: 0rem;
      }
    }
  }
  .bottom {
    p {
        text-align: left;
        margin: 0.15rem 0 0.3rem 0;
    }
  }
}
</style>



